.projects-page {
    max-width: 1200px;
    margin: 0 auto;
}

.projects-hero {
    text-align: center;
    margin-bottom: 64px;
}

.projects-hero h1 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 4rem;
    color: #f5f5f5;
    margin-bottom: 24px;
}

.projects-hero p {
    font-size: 1.2rem;
    color: #a6a6a6;
    max-width: 600px;
    margin: 0 auto;
}

.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 32px;
}

.project-card {
    background: #232323;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
    transition: transform 0.2s, box-shadow 0.2s;
}

.project-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
}

.project-image {
    width: 100%;
    height: 200px;
    overflow: hidden;
}

.project-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project-content {
    padding: 24px;
}

.project-content h2 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.4rem;
    color: #f5f5f5;
    margin-bottom: 12px;
    line-height: 1.3;
}

.project-content p {
    color: #a6a6a6;
    font-size: 0.95rem;
    line-height: 1.5;
    margin-bottom: 16px;
}

.project-technologies {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-bottom: 16px;
}

.tech-tag {
    background: #f5f5f5;
    color: #121212;
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 0.8rem;
    font-weight: 600;
}

.project-link {
    display: inline-block;
    padding: 8px 16px;
    background: #f5f5f5;
    color: #121212;
    text-decoration: none;
    border-radius: 20px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 0.9rem;
    font-weight: 600;
    transition: background 0.2s;
}

.project-link:hover {
    background: #a6a6a6;
}

/* Responsive design */
@media (max-width: 768px) {
    .projects-hero h1 {
        font-size: 3rem;
    }

    .projects-grid {
        grid-template-columns: 1fr;
        gap: 24px;
    }

    .project-card {
        max-width: 400px;
        margin: 0 auto;
    }
} 