.contact-page {
    max-width: 1200px;
    margin: 0 auto;
}

.contact-hero {
    text-align: center;
    margin-bottom: 64px;
}

.contact-hero h1 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 4rem;
    color: #f5f5f5;
    margin-bottom: 24px;
}

.contact-hero p {
    font-size: 1.2rem;
    color: #a6a6a6;
    max-width: 600px;
    margin: 0 auto;
}

.contact-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 64px;
}

.contact-info h2,
.contact-form-container h2 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 2.4rem;
    color: #f5f5f5;
    margin-bottom: 32px;
}

.contact-details {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

.contact-item {
    background: #232323;
    padding: 24px;
    border-radius: 16px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
}

.contact-item h3 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.2rem;
    color: #f5f5f5;
    margin-bottom: 8px;
}

.contact-item p {
    color: #a6a6a6;
    font-size: 1rem;
    line-height: 1.5;
}

.contact-form {
    background: #232323;
    padding: 32px;
    border-radius: 16px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
}

.form-group {
    margin-bottom: 24px;
}

.form-group label {
    display: block;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1rem;
    color: #f5f5f5;
    margin-bottom: 8px;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 12px 16px;
    background: #121212;
    border: 1px solid #333;
    border-radius: 8px;
    color: #f5f5f5;
    font-size: 1rem;
    font-family: 'Nunito', Arial, sans-serif;
    transition: border-color 0.2s;
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: #f5f5f5;
}

.form-group textarea {
    resize: vertical;
    min-height: 120px;
}

.submit-btn {
    background: #f5f5f5;
    color: #121212;
    border: none;
    border-radius: 24px;
    padding: 12px 32px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    transition: background 0.2s;
    width: 100%;
}

.submit-btn:hover {
    background: #a6a6a6;
}

/* Responsive design */
@media (max-width: 768px) {
    .contact-hero h1 {
        font-size: 3rem;
    }

    .contact-content {
        grid-template-columns: 1fr;
        gap: 32px;
    }

    .contact-info h2,
    .contact-form-container h2 {
        font-size: 2rem;
    }
} 