.layout {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.main-content {
    flex: 1;
    max-width: 1200px;
    margin: 0 auto;
    padding: 120px 16px 0 16px;
    width: 100%;
}

/* Responsive design */
@media (max-width: 719px) {
    .main-content {
        padding: 100px 16px 0 16px;
    }
} 