.admin-dashboard {
    max-width: 1200px;
    margin: 0 auto;
}

.dashboard-header {
    text-align: center;
    margin-bottom: 48px;
}

.dashboard-header h1 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 3.2rem;
    color: #f5f5f5;
    margin-bottom: 16px;
}

.dashboard-header p {
    font-size: 1.2rem;
    color: #a6a6a6;
}

.dashboard-tabs {
    display: flex;
    gap: 16px;
    margin-bottom: 32px;
    justify-content: center;
}

.tab {
    background: #232323;
    color: #a6a6a6;
    border: none;
    border-radius: 8px;
    padding: 12px 24px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: background 0.2s, color 0.2s;
}

.tab.active,
.tab:hover {
    background: #f5f5f5;
    color: #121212;
}

.section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
}

.section-header h2 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 2rem;
    color: #f5f5f5;
}

.add-btn {
    background: #f5f5f5;
    color: #121212;
    border: none;
    border-radius: 20px;
    padding: 8px 16px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 0.9rem;
    font-weight: 600;
    cursor: pointer;
    transition: background 0.2s;
}

.add-btn:hover {
    background: #a6a6a6;
}

.table-container {
    background: #232323;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
}

.data-table {
    width: 100%;
    border-collapse: collapse;
}

.data-table th {
    background: #121212;
    color: #f5f5f5;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1rem;
    font-weight: 600;
    padding: 16px;
    text-align: left;
    border-bottom: 1px solid #333;
}

.data-table td {
    padding: 16px;
    color: #a6a6a6;
    border-bottom: 1px solid #333;
}

.data-table tr:hover {
    background: #2a2a2a;
}

.status {
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 0.8rem;
    font-weight: 600;
    text-transform: uppercase;
}

.status.active,
.status.published {
    background: #4caf50;
    color: #fff;
}

.status.inactive,
.status.draft {
    background: #ff9800;
    color: #fff;
}

.action-btn {
    background: #333;
    color: #f5f5f5;
    border: none;
    border-radius: 4px;
    padding: 4px 8px;
    font-size: 0.8rem;
    cursor: pointer;
    margin-right: 8px;
    transition: background 0.2s;
}

.action-btn.edit:hover {
    background: #2196f3;
}

.action-btn.delete:hover {
    background: #f44336;
}

/* Responsive design */
@media (max-width: 768px) {
    .dashboard-header h1 {
        font-size: 2.4rem;
    }

    .dashboard-tabs {
        flex-direction: column;
        align-items: center;
    }

    .section-header {
        flex-direction: column;
        gap: 16px;
        align-items: flex-start;
    }

    .table-container {
        overflow-x: auto;
    }

    .data-table {
        min-width: 600px;
    }
} 