.blogs-page {
    max-width: 1200px;
    margin: 0 auto;
}

.blogs-hero {
    text-align: center;
    margin-bottom: 64px;
}

.blogs-hero h1 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 4rem;
    color: #f5f5f5;
    margin-bottom: 24px;
}

.blogs-hero p {
    font-size: 1.2rem;
    color: #a6a6a6;
    max-width: 600px;
    margin: 0 auto;
}

.blogs-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 32px;
}

.blog-card {
    background: #232323;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
    transition: transform 0.2s, box-shadow 0.2s;
}

.blog-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
}

.blog-image {
    width: 100%;
    height: 200px;
    overflow: hidden;
}

.blog-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.blog-content {
    padding: 24px;
}

.blog-content h2 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.4rem;
    color: #f5f5f5;
    margin-bottom: 12px;
    line-height: 1.3;
}

.blog-excerpt {
    color: #a6a6a6;
    font-size: 0.95rem;
    line-height: 1.5;
    margin-bottom: 16px;
}

.blog-meta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    font-size: 0.85rem;
    color: #a6a6a6;
}

.read-more {
    display: inline-block;
    padding: 8px 16px;
    background: #f5f5f5;
    color: #121212;
    text-decoration: none;
    border-radius: 20px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 0.9rem;
    font-weight: 600;
    transition: background 0.2s;
}

.read-more:hover {
    background: #a6a6a6;
}

.loading,
.error,
.no-blogs {
    text-align: center;
    padding: 64px 16px;
}

.loading {
    color: #a6a6a6;
    font-size: 1.2rem;
}

.error {
    color: #ff6b6b;
    font-size: 1.2rem;
}

.no-blogs h2 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 2rem;
    color: #f5f5f5;
    margin-bottom: 16px;
}

.no-blogs p {
    color: #a6a6a6;
    font-size: 1.1rem;
}

/* Responsive design */
@media (max-width: 768px) {
    .blogs-hero h1 {
        font-size: 3rem;
    }

    .blogs-grid {
        grid-template-columns: 1fr;
        gap: 24px;
    }

    .blog-card {
        max-width: 400px;
        margin: 0 auto;
    }
} 