.navigation {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 32px;
    margin-right: 48px;
}

.nav-links {
    display: flex;
    gap: 32px;
    list-style: none;
    margin: 0;
    padding: 0;
}

.nav-links li a {
    color: #f5f5f5;
    text-decoration: none;
    font-size: 1.1rem;
    font-family: 'Oswald', Arial, sans-serif;
    letter-spacing: 1px;
    padding: 4px 8px;
    border-radius: 4px;
    transition: background 0.2s;
}

.nav-links li a.active,
.nav-links li a:hover {
    background: #232323;
}

.cta-btn {
    background: #f5f5f5;
    color: #121212;
    border: none;
    border-radius: 24px;
    padding: 10px 28px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1rem;
    font-weight: 700;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    margin-left: 32px;
    transition: background 0.2s, color 0.2s;
    text-decoration: none;
    display: inline-block;
}

.cta-btn:hover {
    background: #a6a6a6;
    color: #121212;
}

/* Responsive design */
@media (max-width: 719px) {
    .navigation {
        margin-right: 16px;
        gap: 16px;
    }

    .nav-links {
        gap: 16px;
    }

    .nav-links li a {
        font-size: 1rem;
        padding: 2px 6px;
    }

    .cta-btn {
        margin-left: 16px;
        padding: 8px 20px;
        font-size: 0.9rem;
    }
} 