.about-page {
    max-width: 1000px;
    margin: 0 auto;
}

.about-hero {
    text-align: center;
    margin-bottom: 64px;
}

.about-hero h1 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 4rem;
    color: #f5f5f5;
    margin-bottom: 24px;
}

.about-intro {
    font-size: 1.3rem;
    color: #a6a6a6;
    line-height: 1.6;
    max-width: 600px;
    margin: 0 auto;
}

.about-content {
    display: flex;
    flex-direction: column;
    gap: 64px;
}

.about-story h2,
.about-skills h2,
.about-experience h2,
.about-languages h2 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 2.4rem;
    color: #f5f5f5;
    margin-bottom: 24px;
}

.about-story p {
    font-size: 1.1rem;
    color: #a6a6a6;
    line-height: 1.6;
    margin-bottom: 20px;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 32px;
}

.skill-category {
    background: #232323;
    padding: 32px;
    border-radius: 16px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
}

.skill-category h3 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.4rem;
    color: #f5f5f5;
    margin-bottom: 20px;
}

.skill-category ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.skill-category li {
    color: #a6a6a6;
    margin-bottom: 8px;
    font-size: 1rem;
    line-height: 1.5;
}

.experience-item {
    background: #232323;
    padding: 32px;
    border-radius: 16px;
    margin-bottom: 24px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
}

.experience-item h3 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.6rem;
    color: #f5f5f5;
    margin-bottom: 8px;
}

.experience-period {
    color: #a6a6a6;
    font-size: 1rem;
    margin-bottom: 16px;
    font-style: italic;
}

.experience-item p {
    color: #a6a6a6;
    line-height: 1.6;
}

.languages-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 16px;
}

.languages-list li {
    background: #232323;
    padding: 16px;
    border-radius: 8px;
    color: #a6a6a6;
    font-size: 1rem;
}

.languages-list strong {
    color: #f5f5f5;
}

/* Responsive design */
@media (max-width: 768px) {
    .about-hero h1 {
        font-size: 3rem;
    }

    .about-intro {
        font-size: 1.1rem;
    }

    .skills-grid {
        grid-template-columns: 1fr;
    }

    .languages-list {
        grid-template-columns: 1fr;
    }
} 