.site-footer {
    background: #121212;
    color: #f5f5f5;
    padding: 64px 0 32px 0;
    margin-top: 64px;
    border-top: 1px solid #232323;
}

.footer-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 16px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 48px;
}

.footer-left h2 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 2.4rem;
    color: #f5f5f5;
    margin-bottom: 24px;
    line-height: 1.2;
}

.footer-left p {
    font-size: 1.1rem;
    color: #a6a6a6;
    margin-bottom: 32px;
    line-height: 1.6;
}

.footer-left .cta-btn {
    background: #f5f5f5;
    color: #121212;
    border: none;
    border-radius: 24px;
    padding: 12px 32px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    transition: background 0.2s, color 0.2s;
    text-decoration: none;
    display: inline-block;
}

.footer-left .cta-btn:hover {
    background: #a6a6a6;
    color: #121212;
}

.footer-center h3 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.8rem;
    color: #f5f5f5;
    margin-bottom: 24px;
}

.footer-center p {
    font-size: 1rem;
    color: #a6a6a6;
    margin-bottom: 8px;
    line-height: 1.6;
}

.footer-center p strong {
    color: #f5f5f5;
}

.footer-center .footer-socials {
    color: #f5f5f5;
    font-weight: 600;
    margin-top: 16px;
}

.footer-right h3 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.8rem;
    color: #f5f5f5;
    margin-bottom: 24px;
}

.footer-right ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.footer-right ul li {
    margin-bottom: 12px;
    font-size: 1rem;
    color: #a6a6a6;
}

.footer-right ul li a {
    color: #a6a6a6;
    text-decoration: none;
    transition: color 0.2s;
}

.footer-right ul li a:hover {
    color: #f5f5f5;
}

.footer-bottom {
    max-width: 1200px;
    margin: 0 auto;
    padding: 32px 16px 0 16px;
    border-top: 1px solid #232323;
    text-align: center;
}

.footer-bottom p {
    color: #a6a6a6;
    font-size: 0.9rem;
    margin: 0;
}

/* Responsive design */
@media (max-width: 1000px) {
    .footer-content {
        grid-template-columns: 1fr 1fr;
        gap: 32px;
    }

    .site-footer {
        padding: 48px 0 24px 0;
    }

    .footer-left h2 {
        font-size: 2rem;
    }
}

@media (max-width: 719px) {
    .footer-content {
        grid-template-columns: 1fr;
        gap: 32px;
    }

    .footer-left h2 {
        font-size: 1.8rem;
    }

    .footer-center h3,
    .footer-right h3 {
        font-size: 1.5rem;
    }
} 