.header {
    width: 100%;
    padding: 32px 0 0 0;
    background: transparent;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}

.header-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.logo h1 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.8rem;
    color: #f5f5f5;
    margin: 0;
    letter-spacing: 1px;
}

.logo span {
    font-family: 'Nunito', Arial, sans-serif;
    font-size: 0.9rem;
    color: #a6a6a6;
    margin-top: -4px;
}

/* Responsive design */
@media (max-width: 719px) {
    .header {
        padding: 16px 0 0 0;
    }

    .header-content {
        padding: 0 16px;
        flex-direction: column;
        gap: 16px;
    }

    .logo h1 {
        font-size: 1.5rem;
    }

    .logo span {
        font-size: 0.8rem;
    }
} 