.hero {
    display: flex;
    align-items: stretch;
    gap: 0;
    margin-top: 32px;
    margin-bottom: 32px;
    position: relative;
    min-height: 420px;
    height: auto;
}

.hero-image-container {
    position: relative;
    width: 420px;
    height: 420px;
    border-radius: 32px;
    overflow: hidden;
    box-shadow: 0 4px 32px rgba(0, 0, 0, 0.5);
    background: #232323;
    z-index: 2;
    margin-left: 120px;
    flex-shrink: 0;
    align-self: stretch;
}

.hero-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 32px;
    display: block;
}

.hero-overlay {
    position: absolute;
    left: 50%;
    bottom: 16px;
    transform: translateX(-50%);
    background: rgba(18, 18, 18, 0.3);
    color: #f5f5f5;
    padding: 16px 32px;
    border-radius: 16px;
    font-size: 1.1rem;
    font-family: 'Nunito', Arial, sans-serif;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
    text-align: center;
    min-width: 220px;
    z-index: 3;
}

.hero-text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    z-index: 4;
    max-width: 420px;
    pointer-events: none;
    width: 100%;
    text-align: left;
    margin-left: 40px;
    align-self: stretch;
}

.hero-subtitle,
.hero-title,
.hero-desc,
.portfolio-btn {
    margin-left: 0;
    text-align: left;
    pointer-events: auto;
}

.hero-subtitle {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 3.2rem;
    color: #a6a6a6;
    letter-spacing: 2px;
    font-weight: 800;
    display: block;
    margin-bottom: -0.5em;
}

.hero-title {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 6.2rem;
    color: #f5f5f5;
    font-weight: 700;
    margin: 0 0 0.1em 0;
    letter-spacing: 3px;
    line-height: 1;
}

.hero-desc {
    font-size: 1.1rem;
    color: #a6a6a6;
    margin-bottom: 24px;
    max-width: 420px;
}

.portfolio-btn {
    display: inline-block;
    padding: 10px 32px;
    background: transparent;
    color: #f5f5f5;
    border: 2px solid #f5f5f5;
    border-radius: 24px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.1rem;
    font-weight: 700;
    text-decoration: none;
    transition: background 0.2s, color 0.2s;
    z-index: 4;
    position: relative;
    pointer-events: auto;
    align-self: flex-start;
}

.portfolio-btn:hover {
    background: #f5f5f5;
    color: #121212;
}

.vertical-title {
    position: absolute;
    left: 0;
    top: 60px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    font-family: 'Oswald', Arial, sans-serif;
    color: #f5f5f5;
    letter-spacing: 2px;
    font-weight: 700;
    text-transform: uppercase;
    gap: 8px;
    writing-mode: vertical-rl;
    text-orientation: mixed;
    z-index: 5;
    height: 340px;
    justify-content: flex-start;
}

.vertical-FS,
.vertical-DEV {
    font-size: 4rem;
    writing-mode: vertical-rl;
    text-orientation: mixed;
}

.about-section {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 64px;
    margin-top: 64px;
    margin-bottom: 64px;
}

.about-left h2 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 3.2rem;
    color: #f5f5f5;
    margin-bottom: 24px;
}

.about-left p {
    font-size: 1.1rem;
    color: #a6a6a6;
    margin-bottom: 32px;
    line-height: 1.6;
}

.about-details {
    list-style: none;
    padding: 0;
    margin: 0;
}

.about-details li {
    margin-bottom: 12px;
    font-size: 1rem;
    color: #a6a6a6;
}

.about-right {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 24px;
}

.about-card {
    background: #232323;
    padding: 24px;
    border-radius: 16px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
}

.about-card h3 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 1.4rem;
    color: #f5f5f5;
    margin-bottom: 16px;
}

.about-card p {
    font-size: 0.9rem;
    color: #a6a6a6;
    line-height: 1.5;
}

/* Responsive design */
@media (max-width: 1900px) {
    .hero-image-container {
        width: 360px;
        height: 360px;
    }

    .hero-text {
        max-width: 360px;
    }

    .hero-subtitle {
        font-size: 2.8rem;
    }

    .hero-title {
        font-size: 5.4rem;
    }

    .hero-desc {
        font-size: 1rem;
    }

    .vertical-title {
        height: 280px;
    }

    .vertical-FS,
    .vertical-DEV {
        font-size: 3.2rem;
    }
}

@media (max-width: 890px) {
    .hero {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .hero-desc {
        text-align: center;
        max-width: 100%;
    }

    .hero-text {
        margin-left: 0;
        margin-top: 32px;
        align-items: center;
        text-align: center;
    }

    .hero-image-container {
        margin-left: 0;
        width: 320px;
        height: 320px;
    }

    .vertical-title {
        display: none;
    }
}

@media (max-width: 719px) {
    .hero {
        margin-top: 16px;
        margin-bottom: 16px;
    }

    .hero-text {
        margin-top: 24px;
    }

    .hero-image-container {
        width: 280px;
        height: 280px;
    }

    .hero-desc {
        font-size: 0.9rem;
        margin-bottom: 20px;
    }

    .portfolio-btn {
        padding: 8px 24px;
        font-size: 1rem;
    }
}

@media (max-width: 628px) {
    .about-section {
        grid-template-columns: 1fr;
        gap: 32px;
    }

    .about-right {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 480px) {
    .hero {
        margin-top: 8px;
        margin-bottom: 8px;
    }

    .hero-desc {
        font-size: 0.85rem;
        margin-bottom: 16px;
    }

    .hero-text {
        margin-top: 16px;
    }

    .about-card h3 {
        font-size: 1.2rem;
    }

    .about-card p {
        font-size: 0.85rem;
    }
} 