body {
  margin: 0;
  padding: 0;
  background: #121212;
  color: #f5f5f5;
  font-family: 'Nunito', Arial, sans-serif;
  line-height: 1.6;
}

* {
  box-sizing: border-box;
}

/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&family=Nunito:wght@400;700&display=swap');

/* Global styles */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Oswald', Arial, sans-serif;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

/* Utility classes */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
}

.text-center {
  text-align: center;
}

.mb-0 { margin-bottom: 0; }
.mb-1 { margin-bottom: 8px; }
.mb-2 { margin-bottom: 16px; }
.mb-3 { margin-bottom: 24px; }
.mb-4 { margin-bottom: 32px; }

.mt-0 { margin-top: 0; }
.mt-1 { margin-top: 8px; }
.mt-2 { margin-top: 16px; }
.mt-3 { margin-top: 24px; }
.mt-4 { margin-top: 32px; }
