.single-blog-page {
    max-width: 800px;
    margin: 0 auto;
}

.blog-post {
    background: #232323;
    border-radius: 16px;
    padding: 32px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
}

.blog-header {
    margin-bottom: 32px;
}

.blog-header h1 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 2.8rem;
    color: #f5f5f5;
    margin-bottom: 16px;
    line-height: 1.2;
}

.blog-meta {
    display: flex;
    gap: 24px;
    font-size: 0.9rem;
    color: #a6a6a6;
}

.blog-date,
.blog-author {
    display: flex;
    align-items: center;
}

.blog-image {
    margin-bottom: 32px;
    border-radius: 12px;
    overflow: hidden;
}

.blog-image img {
    width: 100%;
    height: auto;
    display: block;
}

.blog-content {
    margin-bottom: 32px;
}

.blog-content p {
    font-size: 1.1rem;
    color: #a6a6a6;
    line-height: 1.7;
    margin-bottom: 20px;
}

.blog-footer {
    border-top: 1px solid #333;
    padding-top: 24px;
}

.back-link {
    display: inline-block;
    padding: 8px 16px;
    background: #f5f5f5;
    color: #121212;
    text-decoration: none;
    border-radius: 20px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 0.9rem;
    font-weight: 600;
    transition: background 0.2s;
}

.back-link:hover {
    background: #a6a6a6;
}

.loading,
.error {
    text-align: center;
    padding: 64px 16px;
}

.loading {
    color: #a6a6a6;
    font-size: 1.2rem;
}

.error {
    color: #ff6b6b;
}

.error h1 {
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 2.4rem;
    color: #f5f5f5;
    margin-bottom: 16px;
}

.error p {
    color: #a6a6a6;
    font-size: 1.1rem;
    margin-bottom: 24px;
}

/* Responsive design */
@media (max-width: 768px) {
    .blog-post {
        padding: 24px;
    }

    .blog-header h1 {
        font-size: 2.2rem;
    }

    .blog-meta {
        flex-direction: column;
        gap: 8px;
    }
} 